	module.exports = " <div _v-76371ce9=\"\"> <div class=\"btn-group btn-vue-example\" _v-76371ce9=\"\"> <button class=\"btn btn-default btn-xs btn-title\" _v-76371ce9=\"\">scroll-container</button> <button class=\"btn btn-default btn-xs btn-title\" _v-76371ce9=\"\">内容滚动/可拖动</button> <a href=https://github.com/surmon-china/vue-awesome-swiper/blob/master/examples/32-scroll-container.vue target=_blank class=\"btn btn-primary btn-xs\" _v-76371ce9=\"\">&lt; &gt;</a> </div> <swiper :options=swiperOption _v-76371ce9=\"\"> <swiper-slide _v-76371ce9=\"\"> <h4 _v-76371ce9=\"\">绝顶聪明</h4> <p style=\"font-family:microsoft yahei; line-height:1.8\" _v-76371ce9=\"\">一天下午，我同学在建设银行十分无聊的上班，一个穿得很糟糕的女士(神经病患者)来到他窗口，给了他一张纸条要提款。 <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 纸条上赫然写着 <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> '兹派XX同志于贵银行处提取人民币'. <br _v-76371ce9=\"\"> 然后是l后面N多个零元。 <br _v-76371ce9=\"\"> 落款是***C.P中央办公厅***。 <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 我同学本来想报警,可看该神经病患者女子很认真的样子,想想还是打发给保安算鸟.(~估计保安也是很闲). <br _v-76371ce9=\"\"> 果然,保安对该女子说:'你这张条子想要提款,必须先到对面派出所,找所长盖一个章，他盖完章,你再来取钱就没问题啦。' <br _v-76371ce9=\"\"> 该女子想都没想，直接就向派出所走去了。(这保安还真不一般,平时有点小看他了). <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 大概十多分钟，排队的顾客慢慢多起来的时候，那个女子兴高采烈的回来了，举着那个条子，说：'人家说啦，办公程序简化了，不用所长批条直接就可以取钱啦。' <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 我这个同学一听到这就不住的感叹:pol.ice队伍里真有高人，一句'高调'就给打发回来了。 <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 我这个同学和保安当时就有点傻了，营业大厅有很多人都在，怕她精神病发作起来影响正常的秩序，只好把值班主管找来了。 <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> 主管和女患者在一边聊了几句，问你取钱做什么用呀,女患者说:'取钱买面包,蛋糕,吃的,买穿的'主管指了指不远处的地方，该女子就又高高兴兴地走了。 <br _v-76371ce9=\"\"> 保安去请教'高招',主管当时是这样对女患者说的: '我们这里是建行,只有建房子才能到这里取钱。你取钱买吃的,那肯定是粮食了,要去农行,买穿的等东西,取钱要到工商银行才行！' <br _v-76371ce9=\"\"> 我同学打心眼里佩服呀,到底是当主管的啊!!!! <br _v-76371ce9=\"\"><br _v-76371ce9=\"\"> ………… <br _v-76371ce9=\"\"> ………… <br _v-76371ce9=\"\"> 过了一会儿,该女士又回来了.而且带来了工行的回答:'农行的人说了,这里是农行,只有农民能取钱,我是城市人口,工行的人说了,我们这里是公行,只能公的来取,母的不行!!!!,说我是贱人,要到建行取钱' </p> </swiper-slide> <div class=swiper-scrollbar solt=scrollbar _v-76371ce9=\"\"></div> </swiper> </div> ";

