  <div class="index">

    <!-- banner -->
    <div class="jumbotron">
      <div class="container">
        <div class="text-center">
          <h1>Vue-Awesome-Swiper</h1>
          <h3 style="margin: 40px 0 20px;">Swiper(Slides) component for Vue.js(1.X ~ 2.X)</h3>
          <br>
          <p><a href="https://github.com/surmon-china/vue-awesome-swiper" class="btn btn-primary btn-lg" target="_blank" v-touch-ripple>View on GitHub</a>
            <span>&nbsp;</span>
            <a class="btn btn-warning btn-lg" href="https://github.com/surmon-china/vue-awesome-swiper/tree/master/examples" target="_blank" v-touch-ripple>Example Code</a>
            <span>&nbsp;</span>
            <a class="btn btn-success btn-lg"  href="http://www.swiper.com.cn/api/index.html" target="_blank" v-touch-ripple>api docs (CN)</a>
            <span>&nbsp;</span>
            <a class="btn btn-info btn-lg"  href="http://idangero.us/swiper/api/" target="_blank" v-touch-ripple>api docs (en)</a>
            <span>&nbsp;</span>
            <a class="btn btn-danger btn-lg" @click.stop v-touch-ripple>I need star...</a>
          </p>
        </div>
      </div>
    </div>

    <!-- editor lists -->
    <div class="container">

      <!-- data bind -->
      <div class="row">
        <div class="col-xs-12">
          <div class="panel panel-default">
            <div class="panel-heading" v-touch-ripple>
              <h4 class="text-center" style="text-transform: uppercase;color: #bbb;">
                <span>Example from Swiper3 example</span>
              </h4>
            </div>
            <div class="panel-body">
              <div class="col-xs-12" v-for="(key, val) in examples">
                <div class="swiper-example">
                  <example01 v-if="key == 'example01'"></example01>
                  <example02 v-if="key == 'example02'"></example02>
                  <example03 v-if="key == 'example03'"></example03>
                  <example04 v-if="key == 'example04'"></example04>
                  <example05 v-if="key == 'example05'"></example05>
                  <example06 v-if="key == 'example06'"></example06>
                  <example07 v-if="key == 'example07'"></example07>
                  <example08 v-if="key == 'example08'"></example08>
                  <example09 v-if="key == 'example09'"></example09>
                  <example10 v-if="key == 'example10'"></example10>
                  <example11 v-if="key == 'example11'"></example11>
                  <example12 v-if="key == 'example12'"></example12>
                  <example13 v-if="key == 'example13'"></example13>
                  <example14 v-if="key == 'example14'"></example14>
                  <example15 v-if="key == 'example15'"></example15>
                  <example16 v-if="key == 'example16'"></example16>
                  <example17 v-if="key == 'example17'"></example17>
                  <example18 v-if="key == 'example18'"></example18>
                  <example19 v-if="key == 'example19'"></example19>
                  <example20 v-if="key == 'example20'"></example20>
                  <example21 v-if="key == 'example21'"></example21>
                  <example22 v-if="key == 'example22'"></example22>
                  <example23 v-if="key == 'example23'"></example23>
                  <example24 v-if="key == 'example24'"></example24>
                  <example25 v-if="key == 'example25'"></example25>
                  <example26 v-if="key == 'example26'"></example26>
                  <example27 v-if="key == 'example27'"></example27>
                  <example28 v-if="key == 'example28'"></example28>
                  <example29 v-if="key == 'example29'"></example29>
                  <example30 v-if="key == 'example30'"></example30>
                  <example31 v-if="key == 'example31'"></example31>
                  <example32 v-if="key == 'example32'"></example32>
                  <example33 v-if="key == 'example33'"></example33>
                  <example34 v-if="key == 'example34'"></example34>
                  <example35 v-if="key == 'example35'"></example35>
                  <example36 v-if="key == 'example36'"></example36>
                  <example37 v-if="key == 'example37'"></example37>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import examples from './examples'

  export default {
    components: examples,
    data() {
      return { examples }
    }
  }
</script>

<style lang="scss">

  .swiper-example {
    margin: 20px auto!important;
    position: relative;

    .btn-vue-example {
      position: absolute;
      right: 0;
      top: 0;
      z-index: 999;

      .btn-title {
        font-family: "PingFang SC","Helvetica Neue","Hiragino Sans GB","Segoe UI","Microsoft YaHei","微软雅黑",sans-serif;
        text-transform: capitalize;
      }
    }
  }

  .swiper-container {
    width: 100%;
    height: 300px;
  }
  .swiper-slide {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    text-align: center;
    font-size: 38px;
    font-weight: 700;
    background: #eee;
    
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
</style>


